.programm {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
}

@media (min-width: @mobile-width) {
  .programm {
    text-align: center;
    color: @white;
  }

  .programm__wrapper {
    margin: 0 auto;
    width: 280px;
  }

  .programm__title {
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 25px;
  }

  .programm__slogan {
    font-size: 14px;
    line-height: 14px;
    margin-bottom: 28px;
    font-weight: 400;
  }
}

@media (min-width: @tablet-width) {
  .programm {
    color: #000000;
  }

  .programm__wrapper {
    width: 708px;
    margin: 0 auto;
  }

  .programm__title {
    font-size: 60px;
    line-height: 60px;
    text-align: left;
    margin: 65px 0 41px 88px;
  }

  .programm__slogan {
    font-size: 20px;
    line-height: 20px;
    text-align: left;
    margin: 0 0 20px 88px;
    text-transform: uppercase;
  }

  .programm__image-button {
    margin-left: 88px;
  }

  .programm__image {
    width: 708px;
    height: 609px;
  }
}

@media (min-width: @desktop-width) {
  .programm {
    width: 1220px;
    margin: 0 0 auto;
  }

  .programm__wrapper {
    margin: 0 0 54px 82px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .programm__title {
    font-size: 60px;
    line-height: 60px;
    text-align: left;
    margin: 111px 0 40px 0;
    width: 420px;
  }

  .programm__slogan {
    font-size: 20px;
    line-height: 20px;
    text-align: left;
    margin: 0 0 52px 0;
    width: 420px;
  }

  .programm__image {
    width: 552px;
    height: 499px;
    display: inline;
    position: absolute;
    top: 36px;
    left: 403px;
  }

  .programm__image-button {
    margin: 0 0 185px 0;
  }
}
